.header{
  height: 50px;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  background-color: $base-color;
  z-index: 3;
}
.header-container{
  position: relative;
  text-align: center;
  height: 40px;
  margin: 5px 0;
  line-height: 35px;
  color: $white;
  letter-spacing: 1px;
  font-size: 16px;
  font-weight: 700;
}
.menu-btn{
  position: absolute;
  left:0;
  top: 0;
  font-size: 27px;
  color: $white;
  line-height: 34px;
  padding: 3px 10px;
}
.logo-small{
  position: absolute;
  left: 45px;
  top: 5px;
  width: 30px;
  margin-left: 10px;
}
.action-btn{
  position: absolute;
  right: 0;
  color: $white;
  padding: 4px 11px;
  font-size: 19px;
  line-height: 30px;
}
.content-area{
  padding: 15px;
  margin-top: 50px;
  background-color: #eef5fe;
}
.chart-body{
  .performance-lead, .performance-business, .performance-converted, .performance-meeting{
    height: 200px;
    max-width: 165px;
    margin-left: auto;
    margin-right: auto;
  }
}
.meeting-info-ul{
  li{
    padding-left: 45px;
    position: relative;
    &:before{
      font-family: 'FontAwesome';
      content: "\f10c";
      position: absolute;
      left: 5px;
      top: 6px;
      color: $base-color;
      font-size: 20px;
    }
    &:after{
      content: "";
      position: absolute;
      left: 33px;
      top: 0;
      height:100%;
      width: 1px;
      background-color: #dadada;
    }
  }
}
.meeting-events-time,.meeting-events-note{
  display: block;
}
.meeting-detail-container{
  margin-bottom: 10px;
  .meeting-date{
    color: $grey;
    font-size: 12px;
  }
  .meeting-person{
    font-size: 16px;
    color: $text;
  }
}
.meeting-events-time{
  color: $base-color;
  font-size: 12px;
}
.meeting-events-note{
  color: $grey;
  font-size: 16px;
}
.view-more{
  button{
    font-size: 12px;
    color: $grey;
  }
}
.performance-info{
  margin-bottom: 8px;
  .name,.period{
    color: $grey;
    font-size: 13px;
  }
}

#leads-owl .owl-item .item,#performance-owl-count .owl-item .item{
  text-align: center;
  .circle-container,.circle-container2{
    &>div{
      padding: 19px;
      border-radius: 50%;
      color: #fff;
      height: 60px;
      width: 60px;
      font-size: 17px;
      margin: 0 auto 10px;
    }
    &>span{
      font-size: 13px;
    }
    &.new{
      &>div{
        background-color:#525253 ;
      }
    }
    &.in-progress{
      &>div{
        background-color:#f0d80d;
      }
    }
    &.contacted{
      &>div{
        background-color:#0084e8 ;
      }
    }
    &.pending{
      &>div{
        background-color: #e7870d;
      }
    }
    &.converted{
      &>div{
        background-color: #1bf094;
      }
    }
    &.closed{
      &>div{
        background-color: #adadae;
      }
    }
  }
}
.dash-performance{
  .item{
    position: relative;
    #legenddiv,#legenddiv2,#legenddiv3,#legenddiv4{
      position: absolute;
      width: 100%;
      text-align: center;
      z-index: 1;
      font-size: 11px;
      top: -4px;
      h2{
        color: $base-color;
        font-size: 19px;
        margin-top: 83px;
        margin-bottom: 5px;
      }
      div{
        &:first-child{
          margin-bottom: 56px;
          display: none;
        }
      }
      .title{
        padding-right: 5px;
      }
    }
  }
}
#targetPer,#targetPer2,#targetPer3,#targetPer4{
  text-align: center;
  font-size: 11px;
  position: absolute;
  top: 15px;
  width: 100%;
}

.graph-title-donut{
  font-size: 11px;
}
.donut-container{
  position: relative;
  width: 50%;
  padding-right: 10px;
  float: left;
  &.last{
    padding-right: 0;
  }
}
.circle-container{
  position: relative;
  width: 33.33%;
  padding-right: 10px;
  float: left;
  &.last{
    padding-right: 0;
  }
}
.circle-container2{
  position: relative;
  width: 50%;
  padding-right: 10px;
  float: left;
  &.last{
    padding-right: 0;
  }
}
.serial-container{
  position: relative;
  width: 50%;
  padding-right: 10px;
  float: left;
  &.last{
    padding-right: 0;
  }
}