.lead-add-container{
  background-color: $white;
}
.form-title{
  font-size: 16px;
  text-transform: uppercase;
  font-weight: 400;
  margin-top: 10px;
}
.text-btn{
  font-size: 13px;
}
.lead-title{
  font-size: 17px;
  font-weight: 500;
  margin-bottom: 10px;
}
.lead-history,.lead-branch,.lead-contact, .lead-account{
  position: relative;
  padding-left: 40px;
  &:before{
    font-family: 'FontAwesome';
    position: absolute;
    top: 0;
    left: 5px;
    color: $base-color;
    font-size: 20px;
  }
}
.lead-history{
  &:before{
    content: "\f0f2";
  }
}
.lead-branch{
  &:before{
    content: "\f19c";
  }
}
.lead-contact{
  &:before{
    content: "\f098";
  }
}
.lead-account{
  &:before{
    content: "\f0d6";
  }
}
.lead-progress{
  text-transform: uppercase;
  font-size: 15px;
}
.lead-progress-status{
  color: #b4b4b4;
}
.lead-progress-date{

}
.lead-status-container{
  margin-bottom: 2px;
  font-size: 13px;
}
.lead-view-more{
  a{
    color: $base-color;
    font-size: 13px;
    &:hover,&:focus{
      text-decoration: none;
      color: $base-color;
    }
  }

}
.contact-action{
  margin-top: -5px;
  a{
    font-size: 26px;
    &:hover,&:focus,&:active{
      text-decoration: none;
    }
    .fa-envelope{
      color: $base-color;
    }
    .fa-phone-square{
      color: green;
    }
  }
}
.lead-branch-name{
  color: $grey;
  font-size: 13px;
}
.lead-location{
  font-size: 13px;
  color: $grey;
}
.branch-note{
  margin-top: 15px;
  margin-bottom: 10px;
}
.forward-sales{
  a{
    color: $base-color;
    font-size: 12px;
    &:hover,&:focus{
      text-decoration: none;
      color: $base-color;
    }
  }
}
.note-msg{
  color: $grey;
}