.meeting-container{
  background-color: $white;
}
.input-btn{
  display: flex;
  flex-direction: row;
  div{
    flex: 1;
    padding-left: 10px;
    padding-right: 10px;
    a{
      padding-left: 10px;
      padding-right: 10px;
    }
  }
}
.meeting-view-container{
  .input-field label{
    left: 0;
  }
  ::-webkit-input-placeholder { /* Chrome/Opera/Safari */
    color: $grey;
  }
  ::-moz-placeholder { /* Firefox 19+ */
    color: $grey;
  }
  :-ms-input-placeholder { /* IE 10+ */
    color: $grey;
  }
  :-moz-placeholder { /* Firefox 18- */
    color: $grey;
  }
}
.close-text{
  font-size: 9px;
  display: block;
  width: 44px;
  line-height: 1;
  float: left;
  text-align: left;
  padding-top: 6px;
}
.calendar{
  position: relative;
  &:before{
    position: absolute;
    top: 8px;
    right: 5px;
    color: $base-color;
    font-size: 18px;
    font-family: 'FontAwesome';
    content: "\f073";
  }
}
.timer{
  position: relative;
  &:before{
    position: absolute;
    top: 8px;
    right: 5px;
    color: $base-color;
    font-size: 18px;
    font-family: 'FontAwesome';
    content: "\f017";
  }
}
.call{
  position: relative;
  a{
    position: absolute;
    top: 0;
    right: 5px;
    color: $base-color;
    font-size: 25px;
    &:hover,&:focus{
      color: $base-color;
      text-decoration: none;
    }
  }
}