.animate-in {
  -webkit-animation: fadeIn .3s ease-in;
  animation: fadeIn .3s ease-in;
}
.animate-out {
  -webkit-transition: opacity .3s;
  transition: opacity .3s;
  opacity: 0;
}
@-webkit-keyframes fadeIn {
  from { opacity: 0; }
  to { opacity: 1; }
}
@keyframes fadeIn {
  from { opacity: 0; }
  to { opacity: 1; }
}
