.menu-container {
  background-color: #31373a;
  position: fixed;
  height: 100vh;
  width: 220px;
  display: flex;
  flex-direction: column;
  top: 0;
  left: -220px;
  z-index: 4;
  &:before {

  }

}
.overlay-menu{
  content: "";
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background-color: rgba(0, 0, 0, 0.4);
  z-index: 3;
  display: none;
  //visibility: hidden;
  //&.open{
  //  &:before{
  //    visibility: visible;
  //  }
  //}
}
.user-panel {
  position: relative;
  height: 200px;
  background-color: $base-color;
  text-align: center;
  padding-top: 30px;
  .fa{
    font-size: 85px;
    margin-bottom: 12px;
    color: $white;
  }
  span{
    display: block;
    color: $white;
    font-size: 17px;
  }
}

.menu-ul {
  padding: 0;
  margin: 0;
  list-style: none;
  li {
    background-color: #eef5fe;
    color: #999cab;
    a{
      display: block;
      padding: 14px;
      color: #6d6d6d;
      i{
        font-size: 20px;
        margin-right: 10px;
      }
    }
    //&:first-child {
    //  background-color: $white;
    //  a {
    //    color: $base-color;
    //  }
    //}
     &:nth-last-child(2){
      a{
        border-bottom: 1px solid #313142;
      }
    }
    &:nth-last-child(1), &:nth-last-child(2){
     background-color: #45455d;
      a{
        text-transform: uppercase;
        color: $white;
        margin-left: 10px;
      }
    }
  }
}
.menu-close{
  position: absolute;
  top: 14px;
  right: 5px;
  padding: 10px;
  .fa{
    font-size: 22px;
    line-height: 0;
  }
}